                            <!--</v-menu>-->


                        <!--</v-toolbar>-->

                        <!--Corpo-->
                        <v-card-text class="ma-0 pa-0 lighten-5 text-xs-center">

                            <!--Cabecalho de cima.-->
                            <template>
                                <!--Card Azul com a foto-->
                                <v-layout class="red ma-0">
                                    <v-flex xs12 class="blue" style="height: 220px">

                                        <h3 v-if="isMyProfile" style="position: absolute;margin: 20px 10px"
                                            class="white--text">Meu perfil</h3>
                                        <v-btn

                                            v-else=""
                                            fab=""
                                            flat=""
                                            small=""
                                            style="float: right;"
                                        >
                                            <v-icon class="white--text">phone</v-icon>
                                        </v-btn>

                                        <!--<explicador-show-contact-dialog :user="user" :onExplicadorTopProfile="true"></explicador-show-contact-dialog>-->

                                        <v-card width="100px" style="margin: 170px auto">
                                            <v-card-media :src="img" height="100px">
                                            </v-card-media>
                                        </v-card>

                                    </v-flex>


                                </v-layout>

                                <!--Card de baixo-->
                                <v-layout class="red ma-0">

                                    <v-flex xs12 class="white" style="height: 230px">

                                        <div style="margin-top: 80px"></div>
                                        <h3> {{ nomeUser }}</h3>
                                        <h4> {{ userCourseOrClass }}</h4>
                                        <h4> {{ userInstitution }}</h4>
                                        <h4> {{ userLocation }} </h4>

                                    </v-flex>

                                </v-layout>

                                <!--Total disciplinas-->
                                <v-layout class="ma-0">

                                    <v-flex xs12>

                                        {{ totalSubjects }} disciplinas

                                    </v-flex>

                                </v-layout>
                            </template>

                            <!--Dados Pessoais-->
                            <v-layout class="mt-2" justify-center>

                                <mobile-profile-personal-info :user="user"></mobile-profile-personal-info>

                            </v-layout>

                            <!--Disciplinas-->
                            <v-layout  class="mt-2" justify-center>

                                <mobile-profile-subjects-info :user="user"></mobile-profile-subjects-info>

                            </v-layout>

                            <!--Dados Academicos-->
                            <v-layout class="mt-2" justify-center>

                                <mobile-profile-academic-info :user="user"></mobile-profile-academic-info>

                            </v-layout>

                            <!--Localizacao-->
                            <v-layout class="mt-2" justify-center>

                                <mobile-profile-location-info :user="user"></mobile-profile-location-info>

                            </v-layout>

                            <v-layout class="mt-2 elevation-5">
                                <v-flex xs12>
                                    <p class="mt-2" style="font-size: 8pt;">
                                        Para ver o perfil completo acesse pelo computador</p>
                                    <p><a href="https://explicador.co.mz">explicador.co.mz</a></p>
                                </v-flex>
                            </v-layout>
                        </v-card-text>

                    </v-card>

                </v-flex>

            </v-layout>


        </v-container>

        <!--Design para computadores e outros-->
        <v-container fluid class="pt-1 hidden-md-and-down">

            <v-layout>

                <!--Nav-bar fixo esquerdo-->
                <v-flex xs2 offset-xs2 class="hidden-xs-only">
                    <v-layout class="explicador-bar-fixed">
                        <v-flex xs2 offset-xs2>
                            <v-layout row>
                                <!--Apresentacao do explicador aqui.-->
                                <v-layout>
                                    <v-flex xs12 class="white elevation-5  pb-">

                                        <!--Informacao do meu perfil-->
                                        <v-layout v-if="isMyProfile">
                                            <v-flex xs12 class="white--text green text-xs-center">

                                                <v-layout>
                                                    <!--Nome do explicador-->
                                                    <v-flex xs12 class="ma-0 pa-0">
                                                        <!--Nome do utilizador-->
