                                                    </v-flex>

                                                </v-layout>

                                            </v-flex>
                                        </v-layout>

                                        <v-divider></v-divider>

                                        <!--Imagem-->
                                        <v-layout class="mt-2 pa-2">
                                            <!--Foto-->
                                            <v-flex xs3>
                                                <!--Foto de utilizador-->
                                                <img :src="img" class="img-radius"/>

                                            </v-flex>

                                            <!--Nome do explicador-->
                                            <v-flex xs9 class="ml-2 orange--text">

                                                <!--Nome do utilizador-->
                                                <h2>{{ nomeUser }}</h2>

                                            </v-flex>

                                        </v-layout>

                                        <v-divider class=""></v-divider>

                                        <!--Alguns dados academicos-->
                                        <v-layout>

                                            <v-flex class="mt-3 pa-2">
                                                <!--Instituicao do utilizador-->
                                                <h4 class="pb-2 blue--text"><v-icon>school</v-icon> {{ userInstitution }}</h4>
                                                <h4 class="pb-2 grey--text"><v-icon>school</v-icon> {{ userCourse }}</h4>
                                                <h4 class="pb-2 green--text"><v-icon>school</v-icon> {{ userDepartment }}</h4>

                                            </v-flex>

                                        </v-layout>
                                    </v-flex>
                                </v-layout>

                            </v-layout>
                            <v-layout>
                                <v-flex>

                                    <v-btn block color="white" router @click="scrollTo('#disciplinas')" class="azul-principal--text">Disciplinas</v-btn>

                                    <!--Btn contacto do explicador-->
                                    <v-btn if="!isMyProfile" class="white--text" color='blue' @click="scrollTo('#contact')" block="" title="Ver os contactos do explicador">
                                        <v-icon left dark>phone</v-icon>
                                        Ver Contactos
                                    </v-btn>

                                    <v-btn block color="white" router @click="scrollTo('#dadosPessoais')" class="azul-principal--text">Informaes pessoais</v-btn>

                                    <v-btn v-if="isMyProfile" block color="white" router @click="scrollTo('#fotoDePerfil')" class="azul-principal--text">Imagem do Perfil</v-btn>

                                    <v-btn block color="white" router @click="scrollTo('#dadosAcademicos')" class="azul-principal--text">Informaes Acadmicas</v-btn>

                                    <v-btn block color="white" router @click="scrollTo('#localizacao')" class="azul-principal--text">Localizacao</v-btn>

                                </v-flex>
                            </v-layout>

                            <!--<v-btn round outline block router >Testemunhos</v-btn>-->

                        </v-flex>
                    </v-layout>
                </v-flex>

                <!--Informacao central nao fixa-->
                <v-flex  xs12 sm6 offset-sm1 class="ml-2 mb-3" >

                    <v-container class="white">
                        <v-layout>
                            <v-flex xs12>

                                <!--Seccao do inquerito-->
                                <v-layout v-if="isMyProfile" id="inquerito" class="elevation-2" style="height: 120px">

                                    <v-flex xs12 class="pt-3 elevation-10">
                                        <span style="right: 5px;  margin-left: 10px; font-size: 18pt; color: darkblue">
                                            Ol, deixe a sua opinio neste inqurito com 2 perguntas!
                                        </span>
                                        <v-btn
                                            style="font-size: 7pt"
                                            target="_blank"
                                            title="Clique para  dar sugestoes e criticas"
                                            small=""
                                            flat=""
                                            href="https://docs.google.com/forms/d/e/1FAIpQLSerGzBmhk9hdPUgC9awFRP6bdNETUtfo5rUG8MuywPmqtWIVA/viewform?usp=sf_link"
                                        >
                                            <!--Gostaria de nos ajudar com sugestes? clique aqui. preencher o inqurito de 2 perguntas.-->
                                            https://docs.google.com/forms/d/e/1FAIpQLSerGzBmhk9hdPUgC9awFRP6bdNETUtfo5rUG8MuywPmqtWIVA/viewform?usp=sf_link
                                        </v-btn>
                                        <a href="" target="_blank"></a>
                                    </v-flex>

                                </v-layout>

                                <!--Seccao das disciplinas-->
                                <v-layout id="disciplinas" class="elevation-2" :style="{ height: userExplainSome ? 'auto' : '400px' }">

                                    <explicador-subject-info :user="user"></explicador-subject-info>

                                </v-layout>

                                <!--Sessao do contacto do explicador-->
                                <v-layout v-if="!isMyProfile" id="contact" class="elevation-2" style="height: 450px">

                                    <explicador-contact-info :user="user"></explicador-contact-info>

                                </v-layout>

                                <!--Seccao dos dados pessoais-->
                                <v-layout id="dadosPessoais" class="elevation-2" style="height: 450px">

                                    <explicador-personal-info :user="user"></explicador-personal-info>

                                </v-layout>

                                <v-layout v-if="isMyProfile" id="fotoDePerfil" class="elevation-2" style="height: 450px">

                                    <explicador-photo-info :user="user"></explicador-photo-info>

                                </v-layout>

                                <!--Sessao de Dados Academicos-->
                                <v-layout id="dadosAcademicos" class="elevation-2" style="height: 450px">

                                    <explicador-academic-info :user="user"></explicador-academic-info>

                                </v-layout>

                                <!--Sessao de Dados de Localizacao-->
                                <v-layout id="localizacao" class="elevation-2" style="height: 450px">

                                    <explicador-location-info :user="user"></explicador-location-info>

                                </v-layout>

                                <!--Seccao do inquerito-->
                                <v-layout v-if="isMyProfile && userVerified" id="email-verify" class="mt-2 mb- elevation-2">

                                    <v-flex xs12>
                                        <v-expansion-panel>
                                            <v-expansion-panel-content expand-icon="infos">

                                                <div slot="header">
                                                    <span style="right: 5px;  margin-left: 10px; font-size: 18pt; color: darkblue">
                                                        Vo ainda no verificou o seu email!
                                                    </span>
                                                    <br>
                                                    <v-btn
                                                        small=""
                                                        round=""
                                                        flat=""
                                                        color="green"
                                                        title="Clique para te enviarmos o link de verificacao"
                                                        @click.stop="sendVerificationLink"
                                                    >
                                                        Enviar Link de verificacao

                                                        <v-icon>mail</v-icon>
                                                    </v-btn>
                                                    <!--<div class="mr-"></div>-->
                                                    <v-btn
                                                            small=""
                                                            flat=""
                                                            color="red"
                                                    >
                                                        <v-icon>info</v-icon>
                                                        Saiba mais
                                                    </v-btn>
                                                </div>
                                                <v-card>
                                                    <v-card-text>
                                                        <h3>Porque confirmar os dados?</h3>
                                                        <br>
                                                        <br>
                                                        <p>Utilizadores com dados no confirmados:</p>
                                                        <ul class="ml-5">
                                                            <li>
                                                                <p>No sero confiados</p>
                                                            </li>
                                                            <li>
                                                                <p>Poderao no ser informados sobre muitas novidades</p>
                                                            </li>
                                                            <li>
                                                                <p>Sero ocultados ou sero os ultimos a ser alistados</p>
                                                            </li>
                                                        </ul>
                                                        <br>
                                                        <p  class="pa-2" style="border-left: 2px solid blueviolet; background: rgba(200,200,200,.2)">A confirmao de dados  importante, porque d certeza as pessoas que te procuram de que os seus dados de contactos so vlidos.</p>
                                                        <br>
                                                        <h3>Que dados confirmar?</h3>
                                                        <br>
                                                        <p>Por enquanto confirme apenas o seu email!  bom que faas isso urgente para no perder nada.</p>
                                                        <b>Confirme o seu email, clique no botao acima para te enviamos para o seu email o link de verificacao!</b>

                                                        <br>
                                                        <br>
                                                        <p class="pa-2" style="border-left: 2px solid blueviolet; background: rgba(200,200,200,.1)">
                                                            Pode
                                                            <b title="Clique para te enviarmos o link"
                                                               style="cursor: pointer"
                                                               @click.prevent="sendVerificationLink">
                                                                Clicar aqui
                                                            </b>,
                                                            vai para o seu email, clicar no link longo que vamos te enviar, esse e o link de verificacao
                                                        </p>
                                                    </v-card-text>
                                                </v-card>
                                            </v-expansion-panel-content>
                                        </v-expansion-panel>
                                    </v-flex>

                                </v-layout>

                            </v-flex>
                        </v-layout>
                    </v-container>
                </v-flex>

            </v-layout>


        </v-container>

    </div>

</template>

<script>
    export default {
        props: ['user', 'name'],
        data: () => ({
            hidden: false,
            nameU: '',
            isThis: false,
        }),
        created () {
            this.scrollTo(0)
            if (this.isConfirmMailSent)
            {
                setTimeout(this.mostrarDialog(), 150000);

            }

        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint',
                    controlRoute: false,
                }
            },
            isUser () {
                return this.user !== undefined && this.user !== null
            },
            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },
            totalSubjects ()
            {
                let subjects = this.isUser && this.user.Explicador && this.user.Explicador.subjectDetails ? this.user.Explicador.subjectDetails : []
                let total = 0
                subjects.forEach((sbs) => {
                    total += sbs.subjects.length
                })
                return total
            },
            userLocation() {

                const negative = 'Ainda nao registou a localizacao'

                if (!this.isUser || !this.user.Locations) return negative

                let userBairro = this.user.Locations.bairro
                let isFromMaputoCity = this.user.Locations.isFromMaputoCity
                let isFromMaputoProvince = this.user.Locations.isFromMaputoProvince
                let district = this.user.Locations.district
                let locality = this.user.Locations.locality

                return (isFromMaputoCity || isFromMaputoProvince) && userBairro !== null && userBairro
                        ? userBairro : (locality)
                        ? locality : district
                        ? district : negative

            },
            userInstitution () {

                const defaultTitle = 'Sem Instituicao'

                if (!this.isUser || !this.user.Educations) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primrio' ? userEd.userSchool : defaultTitle
                inst = userEd.level === 'Secundrio' ? userEd.userSchool : inst
                inst = userEd.level === 'Tcnico' ? userEd.institution : inst
                inst = userEd.level === 'Superior' ? userEd.institution : inst
                inst = userEd.level === 'Outro' ? userEd.institution : inst

                return inst
            },
            userCourseOrClass () {

                const defaultTitle = 'Sem Faculdade'

                if (!this.isUser || !this.user.Educations) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primrio' ? userEd.userClass : defaultTitle
                inst = userEd.level === 'Secundrio' ? userEd.userClass : inst
                inst = userEd.level === 'Tcnico' ? userEd.courseNivel : inst
                inst = userEd.level === 'Superior' ? userEd.courseNivel : inst
                inst = userEd.level === 'Outro' ? userEd.userSchool : inst

                return inst
            },
            userAuth () {
                return this.$store.getters.userAuth
            },


            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            userVerified () {
                return !this.user.emailVerified
            },
            gender () {

                const negative = 'Sexo nao definido'

                if (!this.isUser) return negative

                let genero = this.user.gender

                return genero && genero !== null && genero && genero !== '' ? genero : negative


            },
            userExplainSome () {

                if (!this.isUser) return false

                return this.user.Explicador && this.user.Explicador.explainLevels
            },

            userFaculty () {

                const negative = 'Sem Faculdade'

                if (!this.isUser) return negative

                let ed = this.user.Educations
                return ed && ed.faculty ?  'Faculdade de ' + ed.faculty : negative
            },
            userCourse () {

                const negative = 'Sem Curso'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.course ? 'Curso de ' + ed.course : negative

            },
            userDepartment () {

                const negative = 'Sem Departamento'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.department ? ed.department : negative

            },
            userNivel () {

                const negative = 'Sem Nivel'

                if (!this.isUser) return negative

                let ed = this.user.Educations

                return ed && ed.nivel ? ed.nivel : negative


            },
            hasSubjects () {

                if ( !this.isUser ) return false

                return this.user && this.user.Explicador && this.user.Explicador.subjects
            },

            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                return this.user.Educations && this.user.Educations.subjects ? this.user.Educations.subjects : []

            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Location

            },

            userProvince () {

                const negative = 'Provincia No registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Location.province

                return userProv !== null && userProv !== undefined && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade No registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Location.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            route() {
                return this.$route.path
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },

            isConfirmMailSent () {
                return this.$store.getters.getConfirmMailSent
            },

        },
        watch: {

            route(value) {

                window.location.reload(false);

            },
            loading (value) {
                if (!value && !this.error && this.isThis) {

                    this.mostrarDialog()
                    this.isThis = false

                }
            }

        },
        methods: {

            scrollTo (el) {

                if (isNaN(el))
                {
//                    Scroll to a certain element
                    document.querySelector(el).scrollIntoView({
                        behavior: 'smooth'
                    });
                }
                else
                {
                    window.scroll({
                        top: el,
                        left: 10,
                        behavior: 'smooth'
                    });
                }




            },
            sendVerificationLink () {
                this.isThis = true
                this.$store.dispatch('sendVerificationLink')
            },
            mostrarDialog () {

                const title = 'Email de confirmao enviado com sucesso .. .'
                const message = 'Acabamos de te enviar um email. Acesse a caixa de entrada na sua conta de email para confirmar. Te enviamos um link de confirmao, e s clicar. Cumprimentos.'
                const btn1 = ''
                const btn2 = 'Ok'

                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {show: true, title: title, message: message,btn1: btn1,btn2: btn2})
                this.$store.dispatch('confirmationMailSet', false) //mostra que o email de confirmacao ja foi enviado

            },

        },
    }
</script>
<style>

    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }

    .elevation-10 {
        /*box-shadow: 0 6px 6px -3px rgba(0,0,0,.2),0 10px 14px 1px rgba(255,9,5,.6),0 4px 1px 3px rgba(0,0,0,.12)!important;*/
    }

</style>


