<template>

    <div style="width: 100%">

        <v-flex xs12 sm12>

            <v-card>

                <!--Cabecalho-->
                <v-card-title class="azul-principal darken-1">
                    <span class="headline white--text">Informaes Acadmicas</span>

                    <v-spacer></v-spacer>
                    <explicador-edit-academic-dialog
                        v-if="isMyProfile"
                        :user="user"
                        :isMobile="true"
                        ></explicador-edit-academic-dialog>
                    <template v-else="">
                        <v-spacer></v-spacer>
                        <v-btn
                                fab=""
                                flat=""
                                small=""
                                outline=""
                                style="float: right;"
                        >
                            <v-icon class="white--text">more_horiz</v-icon>
                        </v-btn>
                    </template>

                </v-card-title>

                <v-list>
                    <v-list-tile @click="">
                        <v-list-tile-action>
                            <v-icon>school</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>{{ userInstitution }}</v-list-tile-title>
                        </v-list-tile-content>
                        <!--<v-list-tile-action>-->
                        <!--<v-icon>chat</v-icon>-->
                        <!--</v-list-tile-action>-->
                    </v-list-tile>
                    <v-divider inset></v-divider>

                    <v-list-tile @click="">
                        <v-list-tile-action>
                            <v-icon>school</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>{{ userCourseOrClass }}</v-list-tile-title>
                        </v-list-tile-content>
                        <!--<v-list-tile-action>-->
                        <!--<v-icon>chat</v-icon>-->
                        <!--</v-list-tile-action>-->
                    </v-list-tile>

                    <v-divider inset></v-divider>

                    <template v-if="userDepartmentOrState">

                        <v-list-tile @click="">
                            <v-list-tile-action>
                                <v-icon>school</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>{{ userDepartmentOrState }}</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>

                        <v-divider inset></v-divider>

                    </template>


                    <v-list-tile @click="">
                        <v-list-tile-action>
                            <v-icon>school</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>{{ userNivel }}</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>
                </v-list>

            </v-card>

        </v-flex>


    </div>

</template>

<script>
    export default {
        props: ['user', 'name'],
        data: () => ({
            hidden: false,
            nameU: ''
        }),
        created () {
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint'
                }
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },

            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },
            gender () {

                const negative = 'Sexo nao definido'

                if (!this.isUser) return negative

                let genero = this.user.gender

                return genero && genero !== null && genero && genero !== '' ? genero : negative


            },
            hasEducation ()
            {
                if (!this.isUser) return false
                return this.user.Educations && this.user.Educations.level
            },
            titleInstitution () {
                const defaultTitle = 'Universidade'

                if (!this.hasEducation) return defaultTitle

                let titulo = this.user.Educations.level === 'Primrio' ? 'Escola' : defaultTitle
                titulo = this.user.Educations.level === 'Secundrio' ? 'Escola' : titulo
                titulo = this.user.Educations.level === 'Tcnico' ? 'Instituto' : titulo
                titulo = this.user.Educations.level === 'Outro' ? 'Instituicao' : titulo

                return titulo
            },

            titleEducationType () {
                const defaultTitle = 'Curso'

                if (!this.hasEducation) return defaultTitle

                let titulo = this.user.Educations.level === 'Primrio' ? 'Classe' : defaultTitle
                titulo = this.user.Educations.level === 'Secundrio' ? 'Classe' : titulo
                titulo = this.user.Educations.level === 'Tcnico' ? 'Ano' : titulo
                titulo = this.user.Educations.level === 'Outro' ? 'Instituicao' : titulo

                return titulo
            },

            titleEducationStateOrDepartment () {
                const defaultTitle = 'Departamento'

                if (!this.hasEducation) return defaultTitle

                let titulo = this.user.Educations.level === 'Primrio' ? 'Estado' : defaultTitle
                titulo = this.user.Educations.level === 'Secundrio' ? 'Estado' : titulo
                titulo = this.user.Educations.level === 'Tcnico' ? 'Estado' : titulo
                titulo = this.user.Educations.level === 'Outro' ? 'Estado' : titulo

                return titulo
            },

            userInstitution () {

                const defaultTitle = 'Sem Instituicao'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primrio' ? userEd.userSchool : defaultTitle
                inst = userEd.level === 'Secundrio' ? userEd.userSchool : inst
                inst = userEd.level === 'Tcnico' ? userEd.institution : inst
                inst = userEd.level === 'Superior' ? userEd.institution : inst
                inst = userEd.level === 'Outro' ? userEd.institution : inst

                return inst
            },
            userCourseOrClass () {

                const defaultTitle = 'Sem Faculdade'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primrio' ? userEd.userClass : defaultTitle
                inst = userEd.level === 'Secundrio' ? userEd.userClass : inst
                inst = userEd.level === 'Tcnico' ? userEd.courseNivel : inst
                inst = userEd.level === 'Superior' ? userEd.courseNivel : inst
                inst = userEd.level === 'Outro' ? userEd.userSchool : inst

                return inst
            },
            userDepartmentOrState () {

                const defaultTitle = 'Sem Departamento'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primrio' ? userEd.educationState : defaultTitle
                inst = userEd.level === 'Secundrio' ? userEd.educationState : inst
                inst = userEd.level === 'Tcnico' ? userEd.educationState : inst
                inst = userEd.level === 'Superior' ? userEd.department : inst
                inst = userEd.level === 'Outro' ? userEd.educationState : inst

                return inst

            },
            userNivel () {

                const defaultTitle = 'Sem Nivel'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primrio' ? userEd.level : defaultTitle
                inst = userEd.level === 'Secundrio' ? userEd.level : inst
                inst = userEd.level === 'Tcnico' ? userEd.level : inst
                inst = userEd.level === 'Superior' ? userEd.level + ', ' + userEd.courseNivel : inst
                inst = userEd.level === 'Outro' ? userEd.level : inst

                return inst


            },
            hasSubjects () {

                if ( !this.isUser ) return false

                let subjects = this.user.Educations.subjects

                return subjects.length > 0
            },

            subjectsTitle () {

                if (!this.hasSubjects) return 'Ainda no registou as disciplinas que explica'

                return 'Disciplinas do ' + this.nomeUser
            },
            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                let subjects = this.user.Educations.subjects

                return subjects


            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Location

            },

            userProvince () {

                const negative = 'Provincia No registada'

                if (!this.hasSomeLocation) return negative

                let userProv = this.user.Location.province

                return userProv !== null && userProv !== undefined && userProv !== '' ? userProv : negative

            },

            userCity () {

                const negative = 'Cidade No registada'

                if (!this.hasSomeLocation) return negative

                let userCity = this.user.Location.city

                return userCity !== null && userCity !== undefined && userCity !== '' ? userCity : negative

            },

            userBairro () {

                const negative = 'Bairro No registado'

                if (!this.hasSomeLocation) return negative

                let userBairro = this.user.Location.bairro

                return userBairro !== null && userBairro !== undefined && userBairro !== '' ? userBairro : negative

            },

        },
        methods: {

            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },
            click () {
                alert('cliou')
            }

        },
    }
</script>
<style scoped>
    div.card__title {
        margin-top: 0px;
        padding: 0;
        font-size: 8pt;
    }
    .list__tile__title {
        font-size: 11px!important;
    }
    .headline {
        margin-left: 1em;
    }
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }
    .img-radius {
        width: 50px;
        height: 50px!important;
    }
    .container {
        padding: 0px;
    }


</style>


