<template>

        <div>
            <v-btn
                class="mt-5 elevation-0 ml-1"
                 fab
                 bottom
                 left
                 color="blue"
                 dark
                 fixed
                 small=""
                 to="/"
                 slot="activator"
            >
                <v-icon>home</v-icon>
            </v-btn>


            <v-btn
                fab
                bottom
                right
                color="primary"
                dark
                fixed
                small=""
                @click="scrollTo(0)"
            >
                <v-icon>keyboard_arrow_up</v-icon>
            </v-btn>

        </div>

</template>

<script>
    export default {
        methods: {
            scrollTo (top)
            {
                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                })
            }
        }
    }
</script>


